import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { useDispatch } from 'react-redux'
import { addNewProject } from './projectsSlice'
import DateFnsUtils from '@date-io/date-fns'
import {
  Grid,
  Button,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions
} from '@material-ui/core'
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from '@material-ui/pickers'


const AddProjectForm = ({ open, setOpen }) => {
  const dispatch = useDispatch()

  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')
  const [startDate, setStartDate] = useState(new Date())
  const [targetEndDate, setTargetEndDate] = useState(new Date())

  const addProject = async (event) => {
    event.preventDefault()

    const newObject = {
      title,
      description,
      startDate,
      targetEndDate,
    }
    dispatch(addNewProject(newObject))

    setTitle('')
    setDescription('')
    setStartDate(new Date())
    setTargetEndDate(new Date())
    setOpen(false)
  }

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <div>
      <Dialog open={open} onClose={handleClose} aria-labelledby="add-project-label">
        <form onSubmit={addProject}>
          <DialogTitle id="add-project-label">Add New Project</DialogTitle>
          <DialogContent>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  required
                  id="title"
                  label="Title"
                  value={title}
                  onChange={e => setTitle(e.target.value)}
                  fullWidth
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  id="projectDescription"
                  label="Description"
                  value={description}
                  onChange={e => setDescription(e.target.value)}
                  fullWidth
                />
              </Grid>
              <Grid item xs={12}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    required
                    label="Start Date"
                    disableToolbar
                    variant="inline"
                    format="MM/dd/yyyy"
                    margin="normal"
                    id="startDateInput"
                    value={startDate}
                    onChange={date => setStartDate(date)}
                    fullWidth
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                  />
                </MuiPickersUtilsProvider>
              </Grid>
              <Grid item xs={12}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    required
                    disableToolbar
                    label='Target End Date'
                    variant="inline"
                    format="MM/dd/yyyy"
                    margin="normal"
                    id="targetEndDateInput"
                    value={targetEndDate}
                    onChange={date => setTargetEndDate(date)}
                    fullWidth
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                  />
                </MuiPickersUtilsProvider>
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              Cancel
          </Button>
            <Button type="submit" color="primary">
              Submit
          </Button>
          </DialogActions>
        </form>
      </Dialog>
    </div>
  )
}

AddProjectForm.propTypes = {
  open: PropTypes.bool.isRequired,
  setOpen: PropTypes.func.isRequired
}

export default AddProjectForm