import React from 'react'
import AdminNav from './AdminNav'
import AssignedProjectNav from './AssignedProjectNav'
import UnauthenticatedNav from './UnauthenticatedNav'
import UnassignedNav from './UnassignedNav'
import AuthNav from './AuthNav'
import Can from './Can'

import { useSelector } from 'react-redux'

const Navbar = () => {
  const authUser = useSelector(state => state.authUser.data)

  return (
    <div className="nav-container mb-3">
      <nav className="navbar navbar-expand-md navbar-light bg-light">
        <div className="container">
          <Can
            role={authUser.role}
            perform="admin-tabs:view"
            yes={() => <AdminNav />}
          />
          <Can
            role={authUser.role}
            perform="assigned-project-tabs:view"
            yes={() => <AssignedProjectNav />}
          />
          <Can
            role={authUser.role}
            perform="visitor-tab:view"
            yes={() => <UnauthenticatedNav />}
          />
          <Can
            role={authUser.role}
            perform="unassigned-tab:view"
            yes={() => <UnassignedNav />}
          />
          <AuthNav />
        </div>
      </nav>
    </div>
  )
}

export default Navbar