import React from 'react'
import logo from '../images/bug-icon.png'
import { useAuth0 } from '@auth0/auth0-react'

import SignupButton from './SignupButton'

const Hero = () => {
  const { isAuthenticated } = useAuth0()

  let content = isAuthenticated
    ? <div></div>
    : <SignupButton />

  return (
    <div className="text-center hero">
      <img className="mb-3 app-logo" src={logo} alt="React logo" width="1000" />
      <h1 className="mb-4">Defect Tracker (Demo!)</h1>
      <p className="lead mb-5">
        Track, manage, and maintain defects for all your software applications.
    </p>
      {content}
    </div>
  )
}

export default Hero
