import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useAuth0 } from '@auth0/auth0-react'
import { addToken } from '../tokenSlice'
import { useParams, useHistory } from 'react-router-dom'
import { selectDefectById, deleteDefect, fetchDefect } from './defectsSlice'
import { selectAllUsers } from '../users/usersSlice'
import { Fab, Grid } from '@material-ui/core'
import EditIcon from '@material-ui/icons/Edit'
import DeleteIcon from '@material-ui/icons/Delete'

import Can from '../../../components/Can'
import Loading from '../../../components/Loading'
import EditDefectForm from './EditDefectForm'

const Defect = () => {
  const { isAuthenticated, getAccessTokenSilently } = useAuth0()
  const authUser = useSelector(state => state.authUser.data)

  const id = useParams().id
  const history = useHistory()
  const dispatch = useDispatch()

  const defect = useSelector(state => selectDefectById(state, Number(id)))
  const defectStatus = useSelector(({ defects }) => defects.status)
  const defectError = useSelector(({ defects }) => defects.error)
  const users = useSelector(selectAllUsers)
  const userStatus = useSelector(({ users }) => users.status)

  const [readOnly, setReadOnly] = useState(true)

  const showWhenReadOnly = { display: readOnly ? '' : 'none' }

  useEffect(() => {
    if (isAuthenticated) {
      getAccessTokenSilently()
        .then(token => {
          dispatch(addToken(token))
          dispatch(fetchDefect(id))
        })
    }
  }, [id, dispatch, getAccessTokenSilently, isAuthenticated])

  const handleReadOnly = () => {
    setReadOnly(!readOnly)
  }

  const handleDelete = () => {
    const deleteInfo = {
      id,
      projectId: defect.projectId
    }
    dispatch(deleteDefect(deleteInfo))
    if (deleteDefect.fulfilled) {
      history.push('/defects')
    }
  }

  let content

  if (defectStatus === 'loading') {
    content = <Loading />

  } else if (defectStatus === 'succeeded' && userStatus === 'succeeded') {
    const identifier = users.find(user => user.id === defect.identifierId)
    const dev = users.find(user => user.id === defect.assignedDevId)

    content =
      <div className="container">
        <Grid container spacing={1}>
          <Grid item xs={6}>
            <h3>Defect #{id}</h3>
          </Grid>
          <Grid item container justify="flex-end" xs={6} spacing={2}>
            <Can
              role={authUser.role}
              perform="defects:edit"
              data={{
                defectProjectId: defect.projectId,
                userProjectId: authUser.projectId
              }}
              yes={() =>
                <Fab
                  onClick={handleReadOnly}
                  color="primary"
                  aria-label="edit"
                  size="medium"
                  style={showWhenReadOnly}
                >
                  <EditIcon />
                </Fab>
              }
            />
            <Can
              role={authUser.role}
              perform="project-member-defects:edit"
              data={{
                userId: authUser.id,
                identifierId: defect.identifierId,
                assignedDevId: defect.assignedDevId
              }}
              yes={() =>
                <Fab
                  onClick={handleReadOnly}
                  color="primary"
                  aria-label="edit"
                  size="medium"
                  style={showWhenReadOnly}
                >
                  <EditIcon />
                </Fab>
              }
            />
            <div style={{ width: 10 }}></div>
            <Can
              role={authUser.role}
              perform="defects:delete"
              data={{
                defectProjectId: defect.projectId,
                userProjectId: authUser.projectId
              }}
              yes={() =>
                <Fab onClick={handleDelete} color="secondary" aria-label="delete" size="medium">
                  <DeleteIcon />
                </Fab>
              }
            />
          </Grid>
          <Grid item xs={12}>
            <EditDefectForm
              defect={defect}
              readOnly={readOnly}
              setReadOnly={handleReadOnly}
              identifier={identifier}
              dev={dev}
              users={users}
            />
          </Grid>
        </Grid>
      </div>
  } else if (defectStatus === 'failed') {
    content = <div>{defectError}</div>
  }

  let defectProject = defect ? defect.projectId : null

  return (
    <Can
      role={authUser.role}
      perform="defects:viewOne"
      data={{
        defectProjectId: defectProject,
        userProjectId: authUser.projectId
      }}
      yes={() => <div>{content}</div>}
      no={() =>
        <div>You do not have permission to view this page.</div>
      }
    />
  )
}

export default Defect