import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Redirect, useHistory } from 'react-router-dom'
import { useAuth0 } from '@auth0/auth0-react'
import { addNewUser } from './usersSlice'
import { fetchAuthenticatedUser } from '../../authenticatedUserSlice'
import {
  Grid,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  DialogContentText
} from '@material-ui/core'

import Can from '../../../components/Can'


const SignUpForm = () => {
  const dispatch = useDispatch()
  const history = useHistory()
  const { user } = useAuth0()

  const [name, setName] = useState('')
  const [occupation, setOccupation] = useState('')

  function addUser(event) {
    event.preventDefault()

    const newValues = {
      name,
      role: 'ADMIN',
      email: user.email,
      occupation
    }

    console.log(" ~ file: UserSignUpForm.js ~ line 36 ~ addUser ~ newValues", newValues)

    dispatch(addNewUser(newValues))
      .then(() => {
        dispatch(fetchAuthenticatedUser())
      })
      .then(() => {
        history.push('/')
      })
  }

  return (
    <div>
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={true}
        aria-labelledby="user-signup-label"
      >
        <form onSubmit={addUser}>
          <DialogTitle id="add-user-label">New User Sign Up</DialogTitle>
          <DialogContent>

            <Grid container spacing={2}>
              <Grid item xs={12}>
                <DialogContentText>
                  You will automatically be assigned an Admin role upon sign up.
                </DialogContentText>
              </Grid>

              <Grid item container justify="center" xs={12}>
                <TextField
                  required
                  id="name"
                  value={name}
                  label="Name"
                  onChange={e => setName(e.target.value)}
                  fullWidth
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
              </Grid>

              <Grid container justify="center" item xs={12}>
                <FormControl
                  fullWidth
                >
                  <InputLabel required shrink id="priority-input-label">
                    Occupation
                  </InputLabel>
                  <Select
                    required
                    labelId="occupation-input-label"
                    id="occupation"
                    value={occupation}
                    onChange={(e) => setOccupation(e.target.value)}
                    placeholder=""
                  >
                    <MenuItem value="Tester">Tester</MenuItem>
                    <MenuItem value="Developer">Developer</MenuItem>
                    <MenuItem value="Manager">Manager</MenuItem>
                  </Select>
                </FormControl>
              </Grid>

              <Grid item container xs={12} justify="center">
                <DialogActions>
                  <Button type="submit" color="primary">
                    Sign Up
                  </Button>
                </DialogActions>
              </Grid>

            </Grid>
          </DialogContent>
        </form>
      </Dialog>
    </div>
  )
}

const UserSignUpForm = () => {
  const authUser = useSelector(({ authUser }) => authUser.data)

  return (
    <Can
      role={authUser.role}
      perform="sign-up:view"
      yes={() => <SignUpForm />}
      no={() => <Redirect to="/" />}
    />
  )
}

export default UserSignUpForm