import {
  createEntityAdapter,
  createSlice,
  createAsyncThunk,
  createSelector
} from '@reduxjs/toolkit'
import { normalize } from 'normalizr'
import { defectSchema } from '../../schemas'
import defectService from '../../../services/defects'


const defectsAdapter = createEntityAdapter()

export const fetchAllDefects = createAsyncThunk(
  'defects/fetchAll',
  async () => {
    const response = await defectService.getAll()
    const normalized = normalize(response.data, [defectSchema])
    return normalized.entities
  }
)

export const fetchDefectsByProject = createAsyncThunk(
  'defects/fetchByProject',
  async (projectId) => {
    const response = await defectService.getByProject(projectId)
    const normalized = normalize(response.data, [defectSchema])
    return normalized.entities
  }
)

// may have to fix in order to retrieve comments
export const fetchDefect = createAsyncThunk(
  'defects/fetchById',
  async (id) => {
    const response = await defectService.getById(id)
    const normalized = normalize(response.data, defectSchema)
    return normalized.entities
  }
)

export const addNewDefect = createAsyncThunk(
  'defects/add',
  async (newDefect) => {
    const response = await defectService.create(newDefect)
    const defectToAdd = {
      ...response.data,
      comments: []
    }
    return defectToAdd
  }
)

export const updateDefect = createAsyncThunk(
  'defects/update',
  async (updateValues, { rejectWithValue }) => {
    const { id } = updateValues
    try {
      const response = await defectService.update(id, updateValues)
      return response.data
    } catch (error) {
      return rejectWithValue(error.response.data)
    }
  }
)

export const deleteDefect = createAsyncThunk(
  'defects/delete',
  async (values) => {
    await defectService.remove(values.id)
    return values
  }
)

const defectsSlice = createSlice({
  name: 'defects',
  initialState: defectsAdapter.getInitialState({
    status: 'idle',
    error: null
  }),
  reducers: {},
  extraReducers: {
    [fetchAllDefects.pending]: (state, action) => {
      state.status = 'loading'
    },
    [fetchAllDefects.fulfilled]: (state, action) => {
      state.status = 'succeeded'
      defectsAdapter.setAll(state, action.payload.defects)
    },
    [fetchAllDefects.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    },
    [fetchDefectsByProject.fulfilled]: (state, action) => {
      if (action.payload.defects) {
        defectsAdapter.upsertMany(state, action.payload.defects)
      }
    },
    [fetchDefect.pending]: (state, action) => {
      state.status = 'loading'
    },
    [fetchDefect.fulfilled]: (state, action) => {
      state.status = 'succeeded'
      defectsAdapter.upsertMany(state, action.payload.defects)
    },
    [fetchDefect.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    },
    [addNewDefect.fulfilled]: (state, action) => {
      defectsAdapter.addOne(state, action.payload)
    },
    [updateDefect.fulfilled]: (state, action) => {
      defectsAdapter.upsertOne(state, action.payload)
    },
    [deleteDefect.fulfilled]: (state, action) => {
      defectsAdapter.removeOne(state, action.payload.id)
      console.log("action payload", action.payload)
    }
  }
})

export default defectsSlice.reducer

export const {
  selectById: selectDefectById,
  selectIds: selectDefectIds,
  selectEntities: selectDefectEntities,
  selectAll: selectAllDefects,
  selectTotal: selectTotalDefects
} = defectsAdapter.getSelectors(state => state.defects)

export const selectCommentsByDefectId = defectId =>
  createSelector(
    [
      state => selectDefectById(state, defectId),
      state => state.comments.ids.map(id => state.comments.entities[id])
    ],
    (defect, comments) => {
      if (!defect) {
        return []
      }
      return Object.keys(comments)
        .map(c => comments[c])
        .filter(comment => defect.comments.includes(comment.id))
    }
  )