import axios from 'axios'
import store from '../app/store'

const baseUrl = `${process.env.REACT_APP_SERVER_URL}/api/comments`

const getToken = () => {
  const state = store.getState()
  return state.token
}

const getAll = () => {
  const token = getToken()
  const config = {
    headers: { Authorization: `Bearer ${token}` }
  }
  return axios.get(baseUrl, config)
}

const getById = (id) => {
  const token = getToken()
  const config = {
    headers: { Authorization: `Bearer ${token}` }
  }
  return axios.get(`${baseUrl}/${id}`, config)
}

const getByDefectId = (defectId) => {
  const token = getToken()
  const config = {
    headers: { Authorization: `Bearer ${token}` }
  }
  return axios.get(`${baseUrl}/defect/${defectId}`, config)
}

const create = newObject => {
  const token = getToken()
  const config = {
    headers: { Authorization: `Bearer ${token}` }
  }
  return axios.post(baseUrl, newObject, config)
}

export default {
  getAll,
  getById,
  getByDefectId,
  create
}