import {
  createEntityAdapter,
  createSlice,
  createAsyncThunk
} from '@reduxjs/toolkit'
import commentsService from '../../../services/comments'

const commentsAdapter = createEntityAdapter()

export const fetchCommentsByDefect = createAsyncThunk(
  'comments/fetchByDefect',
  async (defectId) => {
    const response = await commentsService.getByDefectId(defectId)
    return response.data
  }
)

const commentsSlice = createSlice({
  name: 'comments',
  initialState: commentsAdapter.getInitialState(),
  reducers: {},
  extraReducers: {
    [fetchCommentsByDefect.fulfilled]: (state, action) => {
      commentsAdapter.upsertMany(state, action.payload)
    }
  }
})

export default commentsSlice.reducer

export const {
  selectById: selectCommentById,
  selectIds: selectCommentIds,
  selectEntities: selectCommentEntities,
  selectAll: selectAllComments,
  selectTotal: selectTotalComments,
} = commentsAdapter.getSelectors(state => state.comments)

