import React from 'react'
import { useSelector } from 'react-redux'

import Can from '../components/Can'
import AdminDefectList from '../app/features/defects/AdminDefectList'
import SingleProjectDefectList from '../app/features/defects/SingleProjectDefectList'

const Defects = () => {
  const authUser = useSelector(({ authUser }) => authUser.data)

  return (
    <div>
      <Can
        role={authUser.role}
        perform="admin-defects:list"
        yes={() => <AdminDefectList />}
      />
      <Can
        role={authUser.role}
        perform="assigned-project-defects:list"
        yes={() => <SingleProjectDefectList projectId={authUser.projectId} />}
      />
    </div>
  )
}

export default Defects