import React, { useState, useEffect } from 'react'
import _ from 'lodash'
import { useAuth0 } from '@auth0/auth0-react'
import { addToken } from '../tokenSlice'
import { useSelector, useDispatch } from 'react-redux'
import { fetchDefectsByProject } from './defectsSlice'
import { selectProjectById, selectDefectsByProjectId } from '../projects/projectsSlice'
import { Grid } from '@material-ui/core'
import { Button } from 'react-bootstrap'

import Can from '../../../components/Can'
import DefectTable from './DefectTable'
import AddDefectForm from './AddDefectForm'

const SingleProjectDefectList = ({ projectId }) => {
  const dispatch = useDispatch()
  const { isAuthenticated, getAccessTokenSilently } = useAuth0()
  const authUser = useSelector(state => state.authUser.data)

  const project = useSelector(state => selectProjectById(state, projectId))
  const defects = useSelector(selectDefectsByProjectId(projectId))
  const mutableDefects = _.cloneDeep(defects)

  useEffect(() => {
    if (isAuthenticated) {
      getAccessTokenSilently()
        .then(token => {
          dispatch(addToken(token))
          dispatch(fetchDefectsByProject(projectId))
        })
    }
  }, [isAuthenticated, getAccessTokenSilently, dispatch, projectId])

  const [defectFormOpen, setDefectFormOpen] = useState(false)

  let projectTitle = project ? project.title : ''

  return (
    <div>
      <Grid container spacing={3}>
        <Grid item container xs={12} justify="flex-end">
          <Can
            role={authUser.role}
            perform="defects:create"
            data={{
              defectProjectId: projectId,
              userProjectId: authUser.projectId
            }}
            yes={() =>
              <Button
                variant="primary"
                onClick={() => setDefectFormOpen(true)}
                size="sm"
                style={{ height: 40 }}
              >
                Add Defect
                </Button>
            }
          />
        </Grid>
        <Grid item xs={12}>
          <DefectTable
            data={mutableDefects}
            projectTitle={projectTitle}
          />
          <AddDefectForm
            open={defectFormOpen}
            setOpen={setDefectFormOpen}
            projectId={projectId}
            projectTitle={projectTitle}
          />
        </Grid>
      </Grid>
    </div>
  )
}

export default SingleProjectDefectList