import React from 'react'
import { useSelector } from 'react-redux'
import _ from 'lodash'
import { selectAllUsers } from '../app/features/users/usersSlice'

import Can from '../components/Can'
import UserTable from '../app/features/users/UserTable'

// Need to write logic merging project and user data 
const Users = () => {
  const authUser = useSelector(({ authUser }) => authUser.data)
  const users = useSelector(selectAllUsers)
  const mutableUsers = _.cloneDeep(users)

  return (
    <Can
      role={authUser.role}
      perform="users:list"
      yes={() =>
        <div className="container">
          <UserTable
            data={mutableUsers}
            title="All Users"
          />
        </div>
      }
      no={() => <div>You do not have permission to visit this page.</div>}
    />
  )
}

export default Users
