import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { updateProject } from './projectsSlice'
import DateFnsUtils from '@date-io/date-fns'
import {
  Button,
  Grid,
  TextField
} from '@material-ui/core'
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from '@material-ui/pickers'

const EditProjectForm = ({ project, readOnly, setReadOnly }) => {
  const dispatch = useDispatch()

  const [title, setTitle] = useState(project.title)
  const [description, setDescription] = useState(project.description)
  const [startDate, setStartDate] = useState(project.startDate)
  const [targetEndDate, setTargetEndDate] = useState(project.targetEndDate)
  const [actualEndDate, setActualEndDate] = useState(project.actualEndDate)

  const handleSubmitEdit = (event) => {
    event.preventDefault()
    const updateValues = {
      id: project.id,
      title,
      description,
      startDate,
      targetEndDate,
      actualEndDate
    }

    dispatch(updateProject(updateValues))
    setReadOnly()
  }

  const handleCancelEdit = () => {
    setReadOnly()
    setTitle(project.title)
    setDescription(project.description)
    setStartDate(project.startDate)
    setTargetEndDate(project.targetEndDate)
    setActualEndDate(project.actualEndDate)
  }

  return (
    <form onSubmit={handleSubmitEdit}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <TextField
            id="title"
            label="Project Title"
            value={title}
            InputProps={{
              readOnly: readOnly,
              disableUnderline: readOnly
            }}
            fullWidth
            onChange={(event) => setTitle(event.target.value)}
            required
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            id="description"
            multiline
            label="Description"
            value={description}
            InputProps={{
              readOnly: readOnly,
              disableUnderline: readOnly
            }}
            fullWidth
            onChange={(e) => setDescription(e.target.value)}
          />
        </Grid>
        <Grid item xs={6} md={4}>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker
              required
              clearable
              label="Start Date"
              format="MM/dd/yyyy"
              value={startDate}
              onChange={date => setStartDate(date)}
              inputProps={{
                readOnly: readOnly,
                disabled: readOnly
              }}
              KeyboardButtonProps={{
                'aria-label': 'change date',
                disabled: readOnly,
                readOnly: readOnly
              }}
              fullWidth
            />
          </MuiPickersUtilsProvider>
        </Grid>
        <Grid item xs={6} md={4}>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker
              required
              clearable
              label="Target End Date"
              format="MM/dd/yyyy"
              value={targetEndDate}
              onChange={date => setTargetEndDate(date)}
              inputProps={{
                readOnly: readOnly,
                disabled: readOnly,
              }}
              KeyboardButtonProps={{
                'aria-label': 'change date',
                disabled: readOnly,
                readOnly: readOnly
              }}
              fullWidth
            />
          </MuiPickersUtilsProvider>
        </Grid>
        <Grid item xs={12} md={4}>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker
              clearable
              label="Actual End Date"
              format="MM/dd/yyyy"
              value={actualEndDate}
              onChange={date => setActualEndDate(date)}
              inputProps={{
                readOnly: readOnly,
                disabled: readOnly
              }}
              KeyboardButtonProps={{
                'aria-label': 'change date',
                disabled: readOnly,
                readOnly: readOnly
              }}
              fullWidth
            />
          </MuiPickersUtilsProvider>
        </Grid>
        <Grid container padding={3} spacing={1}>
          <Grid item xs={6} container justify="flex-end">
            {
              !readOnly &&
              <Button
                variant="contained"
                type="submit"
                color="primary"
                style={{ display: { readOnly } }}
              >
                Submit
            </Button>
            }
          </Grid>
          <Grid item xs={6} container justify="flex-start">
            {
              !readOnly &&
              <Button
                variant="contained"
                onClick={handleCancelEdit}
                color="inherit"
              >
                Cancel
            </Button>
            }
          </Grid>
        </Grid>
      </Grid>
    </form>
  )
}

export default EditProjectForm