import React, { useEffect } from 'react'
import { Route, Switch } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { fetchAuthenticatedUser } from './app/authenticatedUserSlice'
import { fetchAllProjects } from './app/features/projects/projectsSlice'
import { fetchAllUsers } from './app/features/users/usersSlice'
import { addToken } from './app/features/tokenSlice'
import { useAuth0 } from '@auth0/auth0-react'

import { Navbar, Footer, Loading } from './components'
import { Home, Projects, Defects, Users } from './views'
import ProtectedRoute from './auth/ProtectedRoute'
import SingleDefect from './app/features/defects/SingleDefect'
import SingleProject from './app/features/projects/SingleProject'
import UserSignUpForm from './app/features/users/UserSignUpForm'

import './app.css'

const App = () => {
  const dispatch = useDispatch()
  const { isLoading, isAuthenticated, getAccessTokenSilently, user } = useAuth0()

  useEffect(() => {
    if (isAuthenticated) {
      getAccessTokenSilently()
        .then(token => {
          dispatch(addToken(token))
          dispatch(fetchAuthenticatedUser())
          dispatch(fetchAllProjects())
          dispatch(fetchAllUsers())
        })
    }
  }, [dispatch, getAccessTokenSilently, isAuthenticated, user])

  if (isLoading) {
    return <Loading />
  }

  return (
    <div id="app" className="d-flex flex-column h-100">
      <Navbar />
      <div className="container-fluid flex-grow-1 flex-shrink-1 mb-5">
        <div className="mt-3">
          <Switch>
            <Route exact path="/" component={Home} />
            <ProtectedRoute exact path="/signup" component={UserSignUpForm} />
            <ProtectedRoute exact path="/projects" component={Projects} />
            <ProtectedRoute exact path="/projects/:id" component={SingleProject} />
            <ProtectedRoute exact path="/users" component={Users} />
            <ProtectedRoute exact path="/defects" component={Defects} />
            <ProtectedRoute exact path="/defects/:id" component={SingleDefect} />
          </Switch>
        </div>
      </div>
      <Footer />
    </div>
  )
}

export default App