import React from 'react'
import { useSelector } from 'react-redux'
import { Redirect } from 'react-router-dom'

import Can from '../components/Can'
import ProjectList from '../app/features/projects/ProjectList'

const Projects = () => {
  const authUser = useSelector(({ authUser }) => authUser.data)
  const authStatus = useSelector(({ authUser }) => authUser.status)

  let assignedProject

  if (authStatus === 'fulfilled') {
    assignedProject = authUser.projectId
  }

  let content =
    assignedProject
      ? <Redirect
        to={`projects/${assignedProject}`}
      />
      : <Can
        role={authUser.role}
        perform="projects:list"
        yes={() => <ProjectList />}
        no={() => <div>You do not have permission to view this page.</div>}
      />
  return content
}

export default Projects