const rules = {
  "VISITOR": {
    static: [
      "home-page:visit",
      "visitor-tab:view"
    ]
  },
  "UNASSIGNED": {
    static: [
      "home-page:visit",
      "unassigned-tab:view",
      "sign-up:view"
    ]
  },
  "ADMIN": {
    static: [
      "admin-tabs:view",
      "projects:list",
      "projects:viewOne",
      "projects:create",
      "projects:delete",
      "projects:edit",
      "users:list",
      "users:viewOne",
      "users:create",
      "users:edit",
      "users:delete",
      "admin-defects:list",
      "defects:viewOne",
      "defects:create",
      "defects:edit",
      "defects:assignDev",
      "defects:delete",
      "comments:list",
      "comments:create",
      "comments:edit",
      "comments:delete",
      "admin-dashboard-page:visit",
      "home-page:visit"
    ],
  },
  "PROJECT LEAD": {
    static: [
      "lead-dashboard-page:visit",
      "home-page:visit",
      "assigned-project-tabs:view",
      "assigned-project-defects:list",
      "comments:list",
      "comments:create",
      "comments:delete",
    ],
    dynamic: {
      "projects:viewOne": ({ projectId, userProjectId }) => {
        if (!projectId || !userProjectId) return false
        return projectId === userProjectId
      },
      "defects:viewOne": ({ defectProjectId, userProjectId }) => {
        if (!defectProjectId || !userProjectId) return false
        return defectProjectId === userProjectId
      },
      "defects:create": ({ defectProjectId, userProjectId }) => {
        if (!defectProjectId || !userProjectId) return false
        return defectProjectId === userProjectId
      },
      "defects:edit": ({ defectProjectId, userProjectId }) => {
        if (!defectProjectId || !userProjectId) return false
        return defectProjectId === userProjectId
      },
      "defects:assignDev": ({ defectProjectId, userProjectId }) => {
        if (!defectProjectId || !userProjectId) return false
        return defectProjectId === userProjectId
      },
      "defects:delete": ({ defectProjectId, userProjectId }) => {
        if (!defectProjectId || !userProjectId) return false
        return defectProjectId === userProjectId
      },
      "comments:edit": ({ userId, authorId }) => {
        if (!userId || !authorId) return false
        return userId === authorId
      }
    }
  },
  "PROJECT MEMBER": {
    static: [
      "member-dashboard-page:visit",
      "home-page:visit",
      "assigned-project-tabs:view",
      "assigned-project-defects:list",
      "comments:list",
      "comments:create",
    ],
    dynamic: {
      "projects:viewOne": ({ projectId, userProjectId }) => {
        if (!projectId || !userProjectId) return false
        return projectId === userProjectId
      },
      "assigned-project-defects:list": ({ defectProjectId, userProjectId }) => {
        if (!defectProjectId || !userProjectId) return false
        return defectProjectId === userProjectId
      },
      "defects:viewOne": ({ defectProjectId, userProjectId }) => {
        if (!defectProjectId || !userProjectId) return false
        return defectProjectId === userProjectId
      },
      "defects:create": ({ defectProjectId, userProjectId }) => {
        if (!defectProjectId || !userProjectId) return false
        return defectProjectId === userProjectId
      },
      "project-member-defects:edit": ({ userId, identifierId, assignedDevId }) => {
        if (!userId || !identifierId) return false
        if (userId === assignedDevId) return true
        return userId === identifierId
      },
      "comments:edit": ({ userId, authorId }) => {
        if (!userId || !authorId) return false
        return userId === authorId
      },
      "comments:delete": ({ userId, authorId }) => {
        if (!userId || !authorId) return false
        return userId === authorId
      }
    }
  }
}

export default rules 