import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import { selectProjectById, selectUsersByProjectId } from './projectsSlice'
import { Fab, Grid } from '@material-ui/core'
import EditIcon from '@material-ui/icons/Edit'

import Can from '../../../components/Can'
import Loading from '../../../components/Loading'
import EditProjectForm from './EditProjectForm'
import UserTable from '../users/UserTable'

const Project = ({ id }) => {
  const authUser = useSelector(({ authUser }) => authUser.data)
  const project = useSelector(state => selectProjectById(state, id))
  const users = useSelector(selectUsersByProjectId(id))

  const projectStatus = useSelector(({ projects }) => projects.status)

  const [readOnly, setReadOnly] = useState(true)

  const showWhenReadOnly = { display: readOnly ? '' : 'none' }

  const handleReadOnly = () => {
    setReadOnly(!readOnly)
  }

  let content

  if (projectStatus === 'loading') {
    content = <Loading />
  } else if (projectStatus === 'succeeded') {
    content =
      <div className="container">
        <Grid container spacing={1}>
          <Grid item xs={6}>
            <h3>{project.title}</h3>
          </Grid>
          <Grid item container justify="flex-end" xs={6} spacing={2}>
            <Can
              role={authUser.role}
              perform="projects:edit"
              yes={() =>
                <Fab
                  onClick={handleReadOnly}
                  color="primary"
                  aria-label="edit"
                  size="medium"
                  style={showWhenReadOnly}
                >
                  <EditIcon />
                </Fab>
              }
            />
          </Grid>
          <Grid item xs={12}>
            <EditProjectForm
              project={project}
              readOnly={readOnly}
              setReadOnly={handleReadOnly}
            />
          </Grid>
          <Grid item xs={12}></Grid>
          <Grid item xs={12} style={{ padding: 10 }}>
            <UserTable
              data={users}
              title="Users in Project"
            />
          </Grid>
        </Grid>
      </div>
  } else if (projectStatus === 'failed') {
    content = <div>projectError</div>
  }

  return (
    <div>
      {content}
    </div>
  )
}

const SingleProject = () => {
  const authUser = useSelector(state => state.authUser.data)
  const authStatus = useSelector(state => state.authUser.status)

  const id = Number(useParams().id)

  let assignedProject =
    authStatus === 'fulfilled'
      ? authUser.projectId
      : null

  return (
    <Can
      role={authUser.role}
      perform="projects:viewOne"
      data={{
        projectId: id,
        userProjectId: assignedProject
      }}
      yes={() => <Project id={id} />}
      no={() => <div className="container">You do not have permission to access this page.</div>}
    />
  )
}

export default SingleProject