import React, { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { selectAllProjects } from '../projects/projectsSlice'
import { changeFilter } from '../filterSlice'
import {
  InputLabel,
  FormControl,
  MenuItem,
  Select
} from '@material-ui/core'


const DefectFilter = () => {
  const [value, setValue] = useState('NONE')
  const dispatch = useDispatch()
  const projects = useSelector(selectAllProjects)

  const handleChange = (event) => {
    dispatch(changeFilter(event.target.value))
    setValue(event.target.value)
  }

  return (
    <FormControl variant="outlined" style={{ 'minWidth': 200 }}>
      <InputLabel id="choose-project-label">Choose Project</InputLabel>
      <Select
        labelId="choose-project-label"
        label="Choose Project"
        id="projectFilter"
        value={value}
        onChange={handleChange}
      >
        <MenuItem value='NONE'>
          <em>None</em>
        </MenuItem>
        {
          projects.map(p =>
            <MenuItem
              key={p.id}
              value={p.id}
            >
              {p.title}
            </MenuItem>
          )
        }
      </Select>
    </FormControl>
  )
}

export default DefectFilter