import {
  createEntityAdapter,
  createSlice,
  createAsyncThunk,
  createSelector
} from '@reduxjs/toolkit'
import { normalize } from 'normalizr'
import { projectSchema } from '../../schemas'
import projectService from '../../../services/projects'
import { addNewDefect, deleteDefect } from '../defects/defectsSlice'

const projectsAdapter = createEntityAdapter()

export const fetchAllProjects = createAsyncThunk(
  'projects/fetchAll',
  async () => {
    const response = await projectService.getAll()
    const normalized = normalize(response.data, [projectSchema])
    return normalized.entities
  }
)

export const addNewProject = createAsyncThunk(
  'projects/add',
  async (newProject) => {
    const response = await projectService.create(newProject)
    return response.data
  }
)

export const updateProject = createAsyncThunk(
  'projects/update',
  async (updateValues, { rejectWithValue }) => {
    const { id } = updateValues
    try {
      const response = await projectService.update(id, updateValues)
      return response.data
    } catch (error) {
      return rejectWithValue(error.response.data)
    }
  }
)

const projectsSlice = createSlice({
  name: 'projects',
  initialState: projectsAdapter.getInitialState({
    status: 'idle',
    error: null
  }),
  reducers: {},
  extraReducers: {
    [fetchAllProjects.pending]: (state, action) => {
      state.status = 'loading'
    },
    [fetchAllProjects.fulfilled]: (state, action) => {
      state.status = 'succeeded'
      projectsAdapter.setAll(state, action.payload.projects)
    },
    [fetchAllProjects.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    },
    [addNewProject.fulfilled]: (state, action) => {
      state.data = state.data.concat(action.payload)
    },
    [updateProject.fulfilled]: (state, action) => {
      projectsAdapter.upsertOne(state, action.payload)
    },
    [addNewDefect.fulfilled]: (state, action) => {
      const id = action.payload.projectId
      const defectId = action.payload.id
      state.entities[id].defects = state.entities[id].defects.concat(defectId)
    },
    [deleteDefect.fulfilled]: (state, action) => {
      const id = action.payload.projectId
      const defectId = action.payload.projectId
      state.entities[id].defects = state.entities[id].defects.filter(id => id !== defectId)
    }
  }
})

export default projectsSlice.reducer

export const {
  selectById: selectProjectById,
  selectIds: selectProjectIds,
  selectEntities: selectProjectEntities,
  selectAll: selectAllProjects,
  selectTotal: selectTotalProjects
} = projectsAdapter.getSelectors(state => state.projects)

export const selectDefectsByProjectId = projectId =>
  createSelector(
    [
      state => selectProjectById(state, projectId),
      state => state.defects.ids.map(id => state.defects.entities[id])
    ],
    (project, defects) => {
      if (!project) {
        return []
      }
      return Object.keys(defects)
        .map(d => defects[d])
        .filter(defect => project.defects.includes(defect.id))
    }
  )

export const selectUsersByProjectId = projectId =>
  createSelector(
    [
      state => selectProjectById(state, projectId),
      state => state.users.ids.map(id => state.users.entities[id])
    ],
    (project, users) => {
      if (!project) {
        return []
      }
      return Object.keys(users)
        .map(u => users[u])
        .filter(user => project.users.includes(user.id))
    }
  )