import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import _ from 'lodash'
import { fetchDefectsByProject } from './defectsSlice'
import { selectProjectById, selectDefectsByProjectId } from '../projects/projectsSlice'
import { Grid, Typography } from '@material-ui/core'
import { Button } from 'react-bootstrap'

import DefectFilter from './DefectFilter'
import DefectTable from './DefectTable'
import AddDefectForm from './AddDefectForm'

const AdminDefectList = () => {
  const dispatch = useDispatch()

  const filter = useSelector(({ filter }) => filter)
  const project = useSelector(state => selectProjectById(state, filter))
  const defects = useSelector(selectDefectsByProjectId(filter))
  const mutableDefects = _.cloneDeep(defects)

  useEffect(() => {
    if (filter !== 'NONE') {
      dispatch(fetchDefectsByProject(filter))
    }
  }, [filter, dispatch])

  const [defectFormOpen, setDefectFormOpen] = useState(false)

  let content

  if (filter === 'NONE') {
    content =
      <div className="container">
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Typography variant="h6" gutterBottom>
              Choose project to view defects
          </Typography>
          </Grid>
          <Grid item xs={12}>
            <DefectFilter />
          </Grid>
        </Grid>
      </div>
  } else {

    let projectId = project ? project.id : ''
    let projectTitle = project ? project.title : ''

    content =
      <div>
        <Grid container spacing={3}>
          <Grid item xs={6}>
            <DefectFilter />
          </Grid>
          <Grid item container xs={6} justify="flex-end">
            <Button
              variant="primary"
              onClick={() => setDefectFormOpen(true)}
              size="sm"
              style={{ height: 40 }}
            >
              Add Defect
            </Button>
          </Grid>
          <Grid item xs={12}>
            <DefectTable
              data={mutableDefects}
              projectTitle={projectTitle}
            />
            <AddDefectForm
              open={defectFormOpen}
              setOpen={setDefectFormOpen}
              projectId={projectId}
              projectTitle={projectTitle}
            />
          </Grid>
        </Grid>
      </div>
  }

  return content
}

export default AdminDefectList