import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import userService from '../services/users'

// figure out logic in assigning an "UNASSIGNED ROLE" to authenticated user

const initialState = {
  data: {},
  status: '',
  error: null
}

export const fetchAuthenticatedUser = createAsyncThunk(
  'authenticatedUser/fetch',
  async () => {
    const response = await userService.getAuthenticatedUser()
    return response.data
  }
)

const authenticatedUserSlice = createSlice({
  name: 'authUser',
  initialState: initialState,
  extraReducers: {
    [fetchAuthenticatedUser.pending]: (state, action) => {
      state.status = 'loading'
    },
    [fetchAuthenticatedUser.fulfilled]: (state, action) => {
      const data = action.payload ? action.payload : { role: "UNASSIGNED" }

      state.status = 'fulfilled'
      state.data = data
    },
    [fetchAuthenticatedUser.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    }
  }
})

export default authenticatedUserSlice.reducer

export const selectAuthUser = state => state.authUser.data