import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { updateDefect } from './defectsSlice'
import { selectProjectById } from '../projects/projectsSlice'
import { makeStyles } from '@material-ui/core/styles'
import DateFnsUtils from '@date-io/date-fns'
import {
  Button,
  Grid,
  TextField,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
} from '@material-ui/core'
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from '@material-ui/pickers'
import Autocomplete from '@material-ui/lab/Autocomplete'

import Can from '../../../components/Can'

const useStyles = makeStyles(theme => ({
  root: {
    "& .MuiFormLabel-root": {
      color: "#002884",
    },
  },
}))

const EditDefectForm = ({ defect, readOnly, setReadOnly, identifier, dev, users }) => {
  const classes = useStyles()
  const dispatch = useDispatch()
  const authUser = useSelector(state => state.authUser.data)

  const project = useSelector(state => selectProjectById(state, defect.projectId))
  const fieldUsers = [...users, { id: null }]

  let projectTitle = project ? project.title : ''
  let devObject = dev ? dev : { id: null }

  const [summary, setSummary] = useState(defect.summary)
  const [assignedDev, setAssignedDev] = useState(devObject)
  const [description, setDescription] = useState(defect.description)
  const [status, setStatus] = useState(defect.status)
  const [priority, setPriority] = useState(defect.priority)
  const [dateIdentified, setDateIdentified] = useState(defect.dateIdentified)
  const [targetResDate, setTargetResDate] = useState(defect.targetResDate)
  const [actualResDate, setActualResDate] = useState(defect.actualResolutionDate)
  const [progress, setProgress] = useState(defect.progress)

  const handleSubmitEdit = (event) => {
    event.preventDefault()

    const updateValues = {
      ...defect,
      summary,
      description,
      status,
      priority,
      dateIdentified,
      targetResDate,
      actualResDate,
      progress,
      assignedDevId: assignedDev.id
    }
    dispatch(updateDefect(updateValues))
    setReadOnly()
  }

  const handleCancelEdit = () => {
    setReadOnly()
    setSummary(defect.summary)
    setDescription(defect.description)
    setStatus(defect.status)
    setAssignedDev(defect.assignedDev)
    setPriority(defect.priority)
    setDateIdentified(defect.dateIdentified)
    setTargetResDate(defect.targetResolutionDate)
    setActualResDate(defect.actualResDate)
    setProgress(defect.progress)
  }

  return (
    <form onSubmit={handleSubmitEdit}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
        </Grid>

        <Grid item xs={12} md={9}>
          <TextField
            id="summary"
            label="Summary"
            value={summary}
            InputProps={{
              readOnly: readOnly,
            }}
            fullWidth
            className={classes.root}
            onChange={(event) => setSummary(event.target.value)}
            required
          />
        </Grid>

        <Grid item xs={6} md={3}>
          <FormControl
            fullWidth
          >
            <InputLabel style={{ color: "#002884" }} shrink id="status-input-label">
              Status
            </InputLabel>
            <Select
              labelId="status-input-label"
              id="status"
              value={status}
              onChange={(e) => setStatus(e.target.value)}
              placeholder={status}
              inputProps={{
                readOnly: readOnly,
              }}
            >
              <MenuItem value='OPEN'>OPEN</MenuItem>
              <MenuItem value='CLOSED'>CLOSED</MenuItem>
            </Select>
          </FormControl>
        </Grid>

        <Grid item xs={6} md={3}>
          <Can
            perform="defects:assignDev"
            data={{
              defectProjectId: defect.projectId,
              userProjectId: authUser.projectId
            }}
            yes={() =>
              <Autocomplete
                style={{ marginTop: -16 }}
                id="user-select"
                options={fieldUsers}
                value={devObject}
                onChange={(e, newValue) => {
                  newValue ? setAssignedDev(newValue) : setAssignedDev({ id: null })
                }}
                getOptionLabel={option => option.name ?? ''}
                getOptionSelected={(option, value) => option.id === value.id}
                renderInput={(params) => (
                  <TextField {...params} label="Assigned Developer" margin="normal" />
                )}
                className={classes.root}
                readOnly={readOnly}
              />
            }
            no={() =>
              <Autocomplete
                style={{ marginTop: -16 }}
                id="user-select"
                options={fieldUsers}
                value={devObject}
                onChange={(e, newValue) => {
                  newValue ? setAssignedDev(newValue) : setAssignedDev({ id: null })
                }}
                getOptionLabel={option => option.name ?? ''}
                getOptionSelected={(option, value) => option.id === value.id}
                renderInput={(params) => (
                  <TextField {...params} label="Assigned Developer" margin="normal" />
                )}
                className={classes.root}
                disabled={!readOnly}
              />
            }
          />
        </Grid>

        <Grid item xs={6} md={3}>
          <FormControl
            fullWidth
          >
            <InputLabel style={{ color: "#002884" }} shrink id="priority-input-label">
              Priority
            </InputLabel>
            <Select
              labelId="priority-input-label"
              id="priority"
              value={priority}
              onChange={(e) => setPriority(e.target.value)}
              placeholder={priority}
              inputProps={{
                readOnly: readOnly,
              }}
            >
              <MenuItem value=''>None</MenuItem>
              <MenuItem value='Low'>Low</MenuItem>
              <MenuItem value='Medium'>Medium</MenuItem>
              <MenuItem value='High'>High</MenuItem>
              <MenuItem value='Immediate'>Immediate</MenuItem>
            </Select>
          </FormControl>
        </Grid>

        <Grid item xs={6} md={3}>
          <TextField
            id="identifier"
            label="Identifier"
            value={identifier.name}
            InputProps={{
              readOnly: true,
              disabled: !readOnly
            }}
            fullWidth
            className={classes.root}
          />
        </Grid>

        <Grid item xs={12} md={3}>
          <TextField
            id="associated-project"
            label="Associated Project"
            value={projectTitle}
            InputProps={{
              readOnly: true,
              disabled: !readOnly
            }}
            fullWidth
            className={classes.root}
          />
        </Grid>

        <Grid item xs={12} sm={6} md={4}>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker
              clearable
              label="Date Identified"
              format="MM/dd/yyyy"
              value={dateIdentified}
              onChange={date => setDateIdentified(date)}
              inputProps={{
                readOnly: readOnly
              }}
              KeyboardButtonProps={{
                'aria-label': 'change date',
                disabled: readOnly,
                readOnly: readOnly
              }}
              fullWidth
              className={classes.root}
            />
          </MuiPickersUtilsProvider>
        </Grid>

        <Grid item xs={12} sm={6} md={4}>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker
              clearable
              label="Target Resolution Date"
              format="MM/dd/yyyy"
              value={targetResDate}
              onChange={date => setTargetResDate(date)}
              inputProps={{
                readOnly: readOnly
              }}
              KeyboardButtonProps={{
                'aria-label': 'change date',
                disabled: readOnly,
                readOnly: readOnly
              }}
              fullWidth
              className={classes.root}
            />
          </MuiPickersUtilsProvider>
        </Grid>

        <Grid item xs={12} md={4}>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker
              clearable
              label="Actual Resolution Date"
              format="MM/dd/yyyy"
              value={actualResDate}
              onChange={date => setActualResDate(date)}
              inputProps={{
                readOnly: readOnly
              }}
              KeyboardButtonProps={{
                'aria-label': 'change date',
                disabled: readOnly,
                readOnly: readOnly
              }}
              fullWidth
              className={classes.root}
            />
          </MuiPickersUtilsProvider>
        </Grid>

        <Grid item xs={12}>
          <TextField
            id="description"
            multiline
            label="Detailed Description"
            value={description}
            InputProps={{
              readOnly: readOnly,
            }}
            fullWidth
            className={classes.root}
            onChange={(e) => setDescription(e.target.value)}
          />
        </Grid>

        <Grid item xs={12}>
          <TextField
            multiline
            id="progress"
            label="Progress"
            value={progress}
            InputProps={{
              readOnly: readOnly,
            }}
            fullWidth
            onChange={(e) => setProgress(e.target.value)}
            className={classes.root}
          />
        </Grid>

        <Grid container padding={3} spacing={1}>
          <Grid item xs={6} container justify="flex-end">
            {
              !readOnly &&
              <Button
                variant="contained"
                type="submit"
                color="primary"
                style={{ display: { readOnly } }}
              >
                Submit
            </Button>
            }
          </Grid>
          <Grid item xs={6} container justify="flex-start">
            {
              !readOnly &&
              <Button
                variant="contained"
                onClick={handleCancelEdit}
                color="inherit"
              >
                Cancel
            </Button>
            }
          </Grid>

        </Grid>
      </Grid>
    </form >
  )
}

export default EditDefectForm