import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { addNewDefect } from './defectsSlice'
import DateFnsUtils from '@date-io/date-fns'
import {
  Button,
  Grid,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle
} from '@material-ui/core'
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from '@material-ui/pickers'


const AddDefectForm = ({ open, setOpen, projectId, projectTitle }) => {
  const dispatch = useDispatch()

  const [summary, setSummary] = useState('')
  const [description, setDescription] = useState('')
  const [priority, setPriority] = useState('')
  const [dateIdentified, setDateIdentified] = useState(new Date())
  const [targetResDate, setTargetResDate] = useState(null)

  const handleClose = () => {
    setSummary('')
    setDescription('')
    setPriority('')
    setDateIdentified(new Date())
    setTargetResDate(null)
    setOpen(false)
  }

  const addDefect = (event) => {
    event.preventDefault()

    const newValues = {
      summary,
      description,
      priority,
      dateIdentified,
      targetResDate,
      projectId
    }

    dispatch(addNewDefect(newValues))
    setSummary('')
    setDescription('')
    setPriority('')
    setDateIdentified(new Date())
    setTargetResDate(null)
    setOpen(false)
  }

  return (
    <div>
      <Dialog open={open} onClose={handleClose} fullWidth maxWidth="sm" aria-labelledby="add-defect-label">
        <form onSubmit={addDefect}>
          <DialogTitle id="add-defect-label"><b>Associated Project:</b> {projectTitle}</DialogTitle>
          <DialogContent>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  autoFocus
                  id="summary"
                  label="Summary"
                  value={summary}
                  onChange={(event) => setSummary(event.target.value)}
                  fullWidth
                  required
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  id="description"
                  multiline
                  label="Description"
                  value={description}
                  fullWidth
                  onChange={(e) => setDescription(e.target.value)}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    clearable
                    label="Date Identified"
                    format="MM/dd/yyyy"
                    value={dateIdentified}
                    onChange={date => setDateIdentified(date)}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                    fullWidth
                  />
                </MuiPickersUtilsProvider>
              </Grid>
              <Grid item xs={12} sm={6}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    clearable
                    label="Target Resolution Date"
                    format="MM/dd/yyyy"
                    value={targetResDate}
                    onChange={date => setTargetResDate(date)}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                    fullWidth
                  />
                </MuiPickersUtilsProvider>
              </Grid>
              <Grid item xs={12}>
                <FormControl
                  fullWidth
                >
                  <InputLabel shrink id="priority-input-label">
                    Priority
                  </InputLabel>
                  <Select
                    labelId="priority-input-label"
                    id="priority"
                    value={priority}
                    onChange={(e) => setPriority(e.target.value)}
                  >
                    <MenuItem value=''>None</MenuItem>
                    <MenuItem value='Low'>Low</MenuItem>
                    <MenuItem value='Medium'>Medium</MenuItem>
                    <MenuItem value='High'>High</MenuItem>
                    <MenuItem value='Immediate'>Immediate</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              Cancel
          </Button>
            <Button type="submit" color="primary">
              Submit
          </Button>
          </DialogActions>
        </form>
      </Dialog>
    </div>
  );
}

export default AddDefectForm